import React from 'react'
import CountryItem from './CountryItem';
import { ReactContext } from './ReactContext';

function CountryContainer() {
    const [page, setPage] = React.useState(0)
    const {
        searchedCountry,
        ItemsReady,
    } = React.useContext(ReactContext)

    let count = 0
    const pageCount = {
    }
    const cuenta = () => {
        searchedCountry.map((element) => {
            const elementSeparator = searchedCountry.indexOf(element)
            if (pageCount[`page_${count}`]) {
                pageCount[`page_${count}`].push(element)
            } else {
                pageCount[`page_${count}`] = []
                pageCount[`page_${count}`].push(element)
            }
            if (elementSeparator % 20 === 0 && elementSeparator > 1) {
                count++
            }
        })
        // console.log(pageCount);


        return pageCount
    }
    // let listItems
    const listItems = Object.entries(cuenta())
    // console.log(listItems);
    return (
        <section id='countrys-container' className='countrys-container'>
            {ItemsReady ?
                (<React.Fragment>
                    <ul className='list-pages'>
                        {listItems.map(element =>
                            <li onClick={() => { setPage(listItems.indexOf(element)) }} key={listItems.indexOf(element)} className='list-pages__item'>{listItems.indexOf(element) + 1}</li>
                        )}
                    </ul>
                    <ul className='list-countries' >
                        {listItems[page][1].map(element =>
                            <CountryItem
                                flag={element.flag}
                                name={element.name}
                                population={element.population}
                                region={element.region}
                                capital={element.capital}
                                key={element.name}
                            />

                        )}
                    </ul>
                </React.Fragment>)
                :
                (<div className='loading'>
                    <p>Loading...</p>
                    <i className='loading__icon' ></i>
                </div>)
            }

        </section>
    )
}
export default CountryContainer